import { makeAutoObservable } from 'mobx';
import { TypesConfig } from './GlobalState.config';

class Comment {
  private comments: TypesConfig[];

  constructor() {
    this.comments = [];
    makeAutoObservable(this);
  }

  get getComment() {
    return this.comments;
  }

  set setComment(comments: TypesConfig[]) {
    this.comments = comments;
  }
}

export default Comment;
