import Discount from './Discount/Discount';
import TryFree from './TryFree/TryFree';

export default function OfferBlock({ setActive, sign }) {
  let block;

  switch (sign) {
    case 'free':
      block = <TryFree setActive={setActive} />;
      break;
    case 'discount':
      block = <Discount setActive={setActive} />;
      break;
  }

  return <>{block}</>;
}
