import { makeAutoObservable } from 'mobx';
import { BasketDeviceConfig } from './GlobalState.config';

class BasketStore {
  private products: BasketDeviceConfig[];
  private sale: number;

  constructor() {
    this.products = [];
    this.sale = 0;
    makeAutoObservable(this);
  }

  get getSale() {
    return this.sale;
  }

  get getProducts() {
    return this.products;
  }

  get count() {
    return this.products.reduce((prev, item) => {
      return (prev += item.quantity);
    }, 0);
  }

  get sum() {
    return this.products.reduce((sum, item) => {
      if (item.sale > 0) {
        return (sum += +(
          +item.price * item.quantity -
          ((+item.price * item.quantity) / 100) * item.sale
        ));
      }
      return (sum += +item.price * item.quantity);
    }, 0);
  }

  get sumSale() {
    return this.products
      .reduce((sum, item) => {
        if (item.sale > 0) {
          sum += +(
            +item.price * item.quantity -
            (+item.price / 100) * (item.sale + this.sale)
          );
        } else {
          sum +=
            +item.price * item.quantity -
            ((+item.price * item.quantity) / 100) * this.sale;
        }
        return sum;
      }, 0)
      .toString()
      .replace(/^(.{0,4}).*/, '$1')
      .replace(/\.$/, '');
  }

  set setSale(arg) {
    this.sale = arg;
  }

  set setProducts(products: BasketDeviceConfig[]) {
    this.products = products;
  }

  clear() {
    this.products = [];
  }

  countOne(id: number) {
    return this.products.filter((item) => item.id == id)[0]?.quantity || 0;
  }
}

export default BasketStore;
