import { makeAutoObservable } from 'mobx';
import { TypesConfig } from './GlobalState.config';

class Tuner {
  private colors: TypesConfig[];
  private onColors: TypesConfig[];
  constructor() {
    this.colors = [];
    this.onColors = [{ name: 'ALL', id: 0 }];
    makeAutoObservable(this);
  }
  get getColors() {
    return this.colors;
  }
  get getOnColor() {
    return this.onColors;
  }

  set setColors(arg: TypesConfig[]) {
    this.colors = arg;
  }
  set setOnColor(arg: TypesConfig[]) {
    this.onColors = arg;
  }
}

export default Tuner;
