import { SignInputAll } from '../../../../../../interface/ui.interface';
import { Button } from '../../../../../../UI/Button/Button';
import { Input, InputPassword } from '../../../../../../UI/Input/Input';
import Loading, { BtnLoading } from '../../../../../../UI/Loading/Loading';
import './style/EnterBody.scss';

const Body: React.FC<SignInputAll> = ({
  setValue,
  register,
  errors,
  loading,
}) => {
  return (
    <div className="enterbody">
      <div className="enterbody__body">
        <Input
          req={true}
          register={register}
          title="Login / Email"
          type="text"
          name="email"
          textFiled="Login / Email"
          errors={errors}
          setValue={setValue}
        />
        <InputPassword
          req={true}
          title="Password"
          name="password"
          register={register}
          type="password"
          textFiled="Your password"
          setValue={setValue}
          errors={errors}
        />
      </div>
      <div className="enterbody__footer">
        <p className="enterbody__forget mini">Forgot password</p>
        <div className="enterbody__btn">
          {loading ? (
            <BtnLoading>
              <Loading />
            </BtnLoading>
          ) : (
            <Button name="sign">
              <p className="mini">Next</p>
            </Button>
          )}
        </div>
      </div>
    </div>
  );
};

export default Body;
