import { observer } from 'mobx-react-lite';
import { useContext, useState, useEffect } from 'react';
import { useLocation } from 'react-router-dom';
import { Context } from '../../../..';
import { $ } from '../../../../components/customeJQ/customeJQ';
import useLoading from '../../../../Hooks/useLoading';
import { CommentAPI } from '../../../../http/commentAPI';
import { RatingAPI } from '../../../../http/RatingAPI';
import Loading, { BtnLoading } from '../../../../UI/Loading/Loading';
import { CommentsConf } from '../../Comments';
import { StarsChange } from '../Start/StarDevice';
import './REVIEWS.scss';

export const REVIEWS: React.FC<ReviewTypes> = observer(({ setElem, model }) => {
  const deviceId = useLocation().pathname.match(/[0-9]*$/gm);
  const [value, setValue] = useState('');
  const { likes, user, comment } = useContext(Context);
  const [rating, setRating] = useState(0);

  const [, fetchingComment] = useLoading(async () => {
    if (deviceId) {
      const response = await CommentAPI.getAll(deviceId[0]);
      comment.setComment = response.sort((a, b) => {
        new Date(a.date) > new Date(b.date);
      });
    }
  });

  const [isLoading, fetching] = useLoading(async () => {
    if (deviceId) {
      const response = await CommentAPI.set(value, deviceId[0]);
      const likesData = await RatingAPI.change(
        user.getUser.id,
        deviceId[0],
        rating
      );
      if (response.message == 'ok' && likesData.message == 'ok') {
        likes.setlikes = likesData.rating;
        $('.comments__item').remove('check');
        $('.comments__posts').add('check');
        setElem('POSTS');
      }
    }
  });

  useEffect(() => {
    fetchingComment();
  }, []);

  const handlerSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    if (user.getAuth) {
      fetching();
    } else {
      model('in', 'sing');
    }
  };

  return (
    <div className="reviews">
      <>
        {comment.getComment.filter((item) => user.getUser.id == item.userId)
          .length == 0 ? (
          <form onSubmit={handlerSubmit} className="reviews__form">
            <legend className="reviews__title">
              <p className="big">YOU'RE REVIEWING:</p>
            </legend>
            <div className="reviews__star">
              <StarsChange
                number={rating}
                setRating={setRating}
                text={'Your score on the product'}
              />
            </div>
            <textarea
              value={value}
              onChange={(e) => {
                setValue(e.target.value);
              }}
              name="text"
            />
            <button className="reviews__btn">
              {isLoading ? (
                <BtnLoading>
                  <Loading />
                </BtnLoading>
              ) : (
                <p className="big">SUBMIT REVIEW</p>
              )}
            </button>
          </form>
        ) : (
          <ReviewAllready />
        )}
      </>
    </div>
  );
});

export const ReviewAllready: React.FC = observer(() => {
  return (
    <div className="reviewallready">
      <div className="reviewallready__text">
        <h2>You Have Already Comment</h2>
      </div>
    </div>
  );
});

type ReviewTypes = Omit<CommentsConf, 'elem'>;
