import { makeAutoObservable } from 'mobx';
import { DeviceConfigAll, TypesConfig } from './GlobalState.config';

class DeviceChange {
  private types: TypesConfig[];
  private device: DeviceConfigAll;
  private brands: TypesConfig[];
  private colors: TypesConfig[];
  private images: TypesConfig[];
  private old: boolean;
  constructor() {
    this.device;
    this.brands;
    this.types;
    this.colors;
    this.images;
    makeAutoObservable(this);
  }

  get getOld() {
    return this.old;
  }

  get getTypes() {
    return this.types;
  }
  get getBrands() {
    return this.brands;
  }
  get getColors() {
    return this.colors;
  }
  get getImages() {
    return this.images;
  }
  get getDevice() {
    return this.device;
  }

  set setOld(what: boolean) {
    this.old = what;
  }

  set setTypes(arg: TypesConfig[]) {
    this.types = arg;
  }
  set setBrands(arg: TypesConfig[]) {
    this.brands = arg;
  }
  set setImages(arg: TypesConfig[]) {
    this.images = arg;
  }
  set setColors(arg: TypesConfig[]) {
    this.colors = arg;
  }
  set setDevice(arg: DeviceConfigAll) {
    this.device = arg;
  }
}

export default DeviceChange;
