import { FieldValues } from 'react-hook-form';
import { makeAutoObservable } from 'mobx';

class User {
  private user: FieldValues;
  private isAuth: boolean;
  private basket: any;
  constructor() {
    this.isAuth = false;
    this.user = {};
    this.basket;
    makeAutoObservable(this);
  }
  set setAuth(auth: boolean) {
    this.isAuth = auth;
  }
  set setUser(user: object) {
    this.user = user;
  }
  set setBasket(arg) {
    this.basket = arg;
  }
  get getBasket() {
    return this.basket;
  }
  get getAuth() {
    return this.isAuth;
  }
  get getUser() {
    return this.user;
  }
}
export default User;
