import { $host } from '.';

export const basketAll = async () => {
  try {
    const { data } = await $host.get('api/basket/');
    return data;
  } catch (error) {
    console.log(error);
  }
};

export const append = async (id: number) => {
  try {
    const { data } = await $host.put(`api/basket/product/${id}/append/1`);
    return data;
  } catch (error) {
    console.log(error);
  }
};

export const increment = async (id: number) => {
  const { data } = await $host.put(`api/basket/product/${id}/increment/1`);
  return data;
};

export const decrement = async (id: number) => {
  const { data } = await $host.put(`api/basket/product/${id}/decrement/1`);
  return data;
};

export const remove = async (id: number) => {
  try {
    const { data } = await $host.put(`api/basket/product/${id}/remove`);
    return data;
  } catch (error) {
    console.log(error);
  }
};

export const clear = async () => {
  try {
    const { data } = await $host.put(`api/basket/clear`);
    return data;
  } catch (error) {
    console.log(error);
  }
};
