import { makeAutoObservable } from 'mobx';
import { DeviceConfigAll, TypesConfig } from './GlobalState.config';

class Device {
  private types: TypesConfig[];
  private device: DeviceConfigAll[];
  private deviceAll: DeviceConfigAll[];
  private deviceAllBrand: DeviceConfigAll[];
  private brands: TypesConfig[];
  private onType: TypesConfig;
  private onBrand: TypesConfig[];
  private page: string;
  private pages: string;
  private sale: boolean;
  constructor() {
    this.brands = [];
    this.sale = false;
    this.types = [];
    this.page = '1';
    this.device = [];
    this.deviceAllBrand = [];
    this.deviceAll = [];
    this.onBrand = [{ name: 'ALL', id: 0 }];
    makeAutoObservable(this);
  }
  get getSale() {
    return this.sale;
  }
  get getPage() {
    return this.page;
  }
  get getPages() {
    return this.pages;
  }
  get getOnType() {
    return this.onType;
  }
  get getOnBrand() {
    return this.onBrand;
  }
  get getTypes() {
    return this.types;
  }
  get getBrands() {
    return this.brands;
  }
  get getDevice() {
    return this.device;
  }
  get getDeviceAll() {
    return this.deviceAll;
  }
  set setPage(arg: string) {
    this.page = arg;
  }
  set setPages(arg: string) {
    this.pages = arg;
  }
  set setOnType(arg: TypesConfig) {
    this.onType = arg;
  }
  set setOnBrand(arg: TypesConfig[]) {
    this.onBrand = arg;
  }
  set setTypes(arg: TypesConfig[]) {
    this.types = arg;
  }
  set setSale(arg: boolean) {
    this.sale = arg;
  }
  set setBrands(arg: TypesConfig[]) {
    this.brands = arg;
  }
  get getDeviceAllBrand() {
    return this.deviceAllBrand;
  }
  setDevice(arg: DeviceConfigAll[], limit: string) {
    this.deviceAllBrand = arg;
    if (arg) {
      this.device = arg.reduce((prev: DeviceConfigAll[], item, index) => {
        if (index >= +this.page * +limit - +limit && prev.length < +limit) {
          prev.push(item);
        }
        return prev;
      }, []);
    }
  }
  changePage(limit: string) {
    this.device = this.deviceAllBrand.reduce(
      (prev: DeviceConfigAll[], item, index) => {
        if (index >= +this.page * +limit - +limit && prev.length < +limit) {
          prev.push(item);
        }
        return prev;
      },
      []
    );
  }
  set setDeviceAll(arg: DeviceConfigAll[]) {
    this.deviceAll = arg;
  }
}

export default Device;
