import { makeAutoObservable } from 'mobx';
import { FavoriteConf } from './GlobalState.config';

class Likes {
  private likes: any;
  private like: any;

  constructor() {
    this.likes = [];
    this.like;

    makeAutoObservable(this);
  }

  get getlikes() {
    return this.likes;
  }

  set setlikes(arg) {
    this.likes = arg;
  }

  number() {
    const number = this.likes.reduce((prev, item) => {
      prev += +item.quantity;
      return prev;
    }, 0);
    return number / this.likes.length;
  }
}

export default Likes;
