import React, { useState, useEffect } from 'react';
import { Swiper, SwiperSlide } from 'swiper/react';
import { ReactComponent as Check } from '../../img/icons/check.svg';

import 'swiper/css';
import 'swiper/css/free-mode';
import 'swiper/css/thumbs';
import './style/Slider.scss';
import 'swiper/css/scrollbar';

import { FreeMode, Navigation, Scrollbar, Thumbs } from 'swiper';
import { API_URL } from '../../components/Routers/links';
import { $ } from '../../components/customeJQ/customeJQ';

export default function SliderApp({ product }) {
  const [thumbsSwiper, setThumbsSwiper] = useState<any>(null!);

  useEffect(() => {
    if (thumbsSwiper && !thumbsSwiper.destroyed) {
      if (
        !$('.mySwiperthumb .swiper-slide').hasAll('swiper-slide-thumb-active')
      ) {
        setTimeout(() => {
          thumbsSwiper.slides[0]?.classList.add('swiper-slide-thumb-active');
        }, 200);
      }
    }
  }, [thumbsSwiper, thumbsSwiper?.slides]);

  return (
    <div className="loop">
      <Swiper
        spaceBetween={10}
        thumbs={{
          swiper: thumbsSwiper && !thumbsSwiper.destroyed ? thumbsSwiper : null,
        }}
        modules={[FreeMode, Navigation, Thumbs]}
        className="mySwiper2"
      >
        {Array.from(
          Object.values(JSON.parse(product?.deviceimg.img || '{}'))
        ).map((item, index) => {
          return (
            <SwiperSlide key={index}>
              <div className="oneproduct__oneblock">
                <div
                  className="oneproduct__img"
                  style={{
                    backgroundImage: `url(${API_URL}${item})`,
                  }}
                ></div>
              </div>
            </SwiperSlide>
          );
        })}
      </Swiper>
      <Swiper
        onSwiper={setThumbsSwiper}
        scrollbar
        spaceBetween={10}
        slidesPerView={3}
        freeMode={true}
        watchSlidesProgress
        modules={[FreeMode, Scrollbar, Thumbs]}
        className="mySwiperthumb"
      >
        {Array.from(
          Object.values(JSON.parse(product?.deviceimg.img || '{}'))
        ).map((item, index) => {
          return (
            <SwiperSlide key={index}>
              <div className="oneproduct__oneblockbottom">
                <div className="oneproduct__check">
                  <Check />
                </div>
                <div className="oneproduct__bottom">
                  <div
                    className="oneproduct__img"
                    style={{
                      backgroundImage: `url(${API_URL}${item})`,
                    }}
                  ></div>
                </div>
              </div>
            </SwiperSlide>
          );
        })}
      </Swiper>
    </div>
  );
}
