import { $host } from './index';

export class RatingAPI {
  static async change(userId, deviceId, quantity) {
    try {
      const { data } = await $host.get('api/rating', {
        params: {
          userId,
          deviceId,
          quantity,
        },
      });
      return data;
    } catch (error) {
      console.log(error);
    }
  }

  static async delete(userId, deviceId) {
    try {
      const { data } = await $host.delete('api/rating', {
        params: {
          userId,
          deviceId,
        },
      });
      return data;
    } catch (error) {
      console.log(error);
    }
  }
}
