import React, { useContext, useEffect, useState, Suspense } from 'react';
import { observer } from 'mobx-react-lite';
import { Context } from './index';
import './style/variable.scss';
import useLoading from './Hooks/useLoading';
import AppRouter from './components/Routers/AppRouter';
import Header from './Pages/Header/Header';
import { addEventScroll, removeEventScroll } from './components/burger/burger';
import Modal from './Pages/Modal/Modal';
import Footer from './Pages/Footer/Footer';
import { ModalType } from './interface/ui.interface';
import { check } from './http/authAPI';
import { basketAll } from './http/basketAPI';
import axios from 'axios';
import { FavoriteAPI } from './http/favoriteAPI';
import Loading from './UI/Loading/Loading';
import FooterDeskont from './Pages/Footer/FooterDeskont';
import 'swiper/css/bundle';

export const ModalContext = React.createContext<ModalType>(null!);

const App: React.FC = observer(() => {
  const [actModal, setActModal] = useState<boolean>(false);
  const [sign, setSign] = useState<string>('');
  const [type, setType] = useState<string>('');
  const [width, setWidth] = useState(0);
  const { user, basket, favorite } =
    useContext<React.ContextType<typeof Context>>(Context);
  const [, fetching] = useLoading(async function () {
    check().then((userData) => {
      if (userData.user) {
        user.setAuth = true;
        user.setUser = userData.user;

        Promise.all([basketAll(), FavoriteAPI.getAll()]).then(
          axios.spread((basketData, favoriteData) => {
            if (basketData.devices && favoriteData.devices) {
              basket.setProducts = basketData.devices;
              favorite.setProducts = favoriteData.devices;
            }
          })
        );
      }
    });
  });

  useEffect(function () {
    fetching();
  }, []);

  useEffect(function () {
    addEventScroll(width, setWidth);
    return () => removeEventScroll();
  }, []);

  const context: ModalType = {
    active: setActModal,
    type: { type: setType, name: setSign },
  };

  return (
    <ModalContext.Provider value={context}>
      <Suspense fallback={<Loading />}>
        <div className="wrapper ">
          <div className="positioncont scroll" data-footer="true">
            <Modal
              active={actModal}
              setActive={setActModal}
              sign={sign}
              type={type}
            />
            <Header />

            <AppRouter />
            <FooterDeskont />
          </div>
        </div>
        <Footer />
      </Suspense>
    </ModalContext.Provider>
  );
});

export default App;
