import { useEffect } from 'react';
import './style/Banner.scss';

export const Banner = () => {
  useEffect(() => {
    document.querySelector('.banner__title')?.classList.add('add');
  });

  return (
    <section className="banner">
      <div className="banner__img"></div>
      <div className="banner__ground">
        <div className="container">
          <div className="banner__menu">
            <div className="banner__body">
              <h2 translate="no" className="banner__title">
                <span>Train</span> <span>hard and</span>
                <span>get results</span>
              </h2>
            </div>
          </div>
        </div>
      </div>
    </section>
  );
};
