import { Swiper, SwiperSlide } from 'swiper/react';
import { Pagination, Autoplay } from 'swiper';
import 'swiper/css';
import 'swiper/css/pagination';
import 'swiper/css/autoplay';

export default function ScheduleSlider() {
  return (
    <Swiper
      loop={true}
      className="swiper-schedule"
      spaceBetween={0}
      slidesPerView={1}
      autoplay={{
        delay: 2000,
        disableOnInteraction: false,
      }}
      pagination={{
        dynamicBullets: true,
      }}
      modules={[Pagination, Autoplay]}
    >
      <SwiperSlide>
        <div className="schedulebanner__back schedulebanner__back_one"></div>
      </SwiperSlide>
      <SwiperSlide>
        <div className="schedulebanner__back schedulebanner__back_two"></div>
      </SwiperSlide>
      <SwiperSlide>
        <div className="schedulebanner__back schedulebanner__back_three"></div>
      </SwiperSlide>
    </Swiper>
  );
}
