import Paragraph from '../../../../../UI/Paragraph/Paragraph';

export default function Sign({ renderModal }) {
  return (
    <ul className="blockicons__sign">
      <li onClick={(e) => renderModal(e, 'in', 'sing')}>
        <a>
          <Paragraph name="link" ru="" en="Sign in" />
        </a>
      </li>
      <li onClick={(e) => renderModal(e, 'up', 'sing')}>
        <a>
          <Paragraph name="link" ru="" en="Sign up" />
        </a>
      </li>
    </ul>
  );
}
