import { observer } from 'mobx-react-lite';
import React, { useEffect, useContext, useState } from 'react';
import { useLocation } from 'react-router-dom';
import { Context } from '../../../..';
import useLoading from '../../../../Hooks/useLoading';
import { CommentAPI } from '../../../../http/commentAPI';
import { ReactComponent as Up } from '../../../../img/icons/thumbs-up.svg';
import { ReactComponent as Down } from '../../../../img/icons/thumbs-down.svg';
import { ReactComponent as Bucket } from '../../../../img/icons/trash.svg';
import { ReactComponent as Reload } from '../../../../img/icons/reload.svg';
import './POSTS.scss';
import Loading from '../../../../UI/Loading/Loading';
import { TypesConfig } from '../../../../mobx/GlobalState.config';
import { $ } from '../../../../components/customeJQ/customeJQ';
import { Stars } from '../Start/StarDevice';
import { PostsItemReload } from './PostItemReload';
import { CommentsConf } from '../../Comments';
import { RatingAPI } from '../../../../http/RatingAPI';

export const POSTS: React.FC<PostTypes> = observer(({ model }) => {
  const deviceId = useLocation().pathname.match(/[0-9]*$/gm);
  const { comment } = useContext(Context);

  const [isLoading, fetching] = useLoading(async () => {
    if (deviceId) {
      const response = await CommentAPI.getAll(deviceId[0]);
      comment.setComment = response.sort((a, b) => {
        new Date(a.date) > new Date(b.date);
      });
    }
  });

  useEffect(() => {
    fetching();
  }, []);

  return (
    <div className="posts">
      {isLoading ? (
        <Loading name="all" />
      ) : comment.getComment.length > 0 ? (
        comment.getComment.map((item: TypesConfig) => {
          return (
            <div key={item.id} className="posts__item">
              <ItemPost
                item={item}
                fetching={fetching}
                model={model}
                deviceId={deviceId}
              />
            </div>
          );
        })
      ) : (
        <div className="posts__notfound">
          <h2>No Related Posts</h2>
        </div>
      )}
    </div>
  );
});

export const ItemPost: React.FC<PostTypes<ReviewTypes>> = observer(
  ({ item, fetching, model, deviceId }) => {
    const { user, likes } = useContext(Context);
    const [reload, setReload] = useState(false);
    const [element, setElement] = useState<TypesConfig>(item);
    const [like, setLike] = useState<TypesConfig>(item.like);
    const [rating, setRating] = useState(0);

    const handlerBasket = async (item) => {
      await CommentAPI.delete(item.id, item.userId);
      const responseLike = await RatingAPI.delete(user.getUser.id, deviceId[0]);
      likes.setlikes = responseLike.device.ratings;
      fetching();
    };

    useEffect(() => {
      if (likes.getlikes.length > 0) {
        setRating(
          likes.getlikes.filter((el) => {
            return el.userId == item.userId;
          })[0]?.quantity
        );
      }
    }, []);

    const handlerClick = async (
      add: boolean,
      e: React.MouseEvent<HTMLElement>
    ) => {
      if (user.getAuth) {
        const response = await CommentAPI.setLike(
          user.getUser.id,
          item.id,
          add
        );
        const target = (e.target as HTMLElement).closest(
          '.posts__like'
        ) as HTMLElement;

        $('.posts__like').remove('add');
        $(target).toggle('add');

        setLike(response);
      } else {
        model('in', 'sing');
      }
    };

    return (
      <>
        {!reload ? (
          <div className="posts__block">
            <div className="posts__head">
              <div className="posts__name">
                <p>{element.user?.firstName}</p>
              </div>
              <div className="posts__date">
                <p className="mini">
                  {element.date.replace(/(:[0-9]{2})(?=\s[PMAM])/g, '')}
                </p>
              </div>
              <div className="posts__rating">
                <Stars number={rating || 0} />
              </div>
            </div>

            <div className="posts__text">
              <p>{element.text}</p>
            </div>
            <div className="posts__icons">
              <div className="posts__thumbs">
                <div
                  className={
                    like.likecomments.filter((el) => {
                      return el.userId == element.userId;
                    }).length > 0
                      ? 'add posts__like '
                      : ' posts__like '
                  }
                >
                  <div
                    onClick={(e) => handlerClick(true, e)}
                    className="posts__icon"
                  >
                    <div className="posts__svg">
                      <Up />
                    </div>
                  </div>
                  <div className="posts__countlike">
                    <p>{like.quantityLike}</p>
                  </div>
                </div>
                <div
                  className={
                    like.dislikes.filter((el) => {
                      return el.userId == element.userId;
                    }).length > 0
                      ? 'add posts__like '
                      : ' posts__like '
                  }
                >
                  <div
                    onClick={(e) => handlerClick(false, e)}
                    className="posts__icon "
                  >
                    <div className="posts__svg down">
                      <Down />
                    </div>
                  </div>
                  <div className="posts__countlike">
                    <p>{like.quantityDis}</p>
                  </div>
                </div>
              </div>
              {+user.getUser.id == element.userId ||
              user.getUser.role == 'Admin' ? (
                <div className="posts__change">
                  <div
                    onClick={() => handlerBasket(element)}
                    className="posts__basket posts__svg"
                  >
                    <Bucket />
                  </div>
                  {+user.getUser.id == element.userId && (
                    <div
                      onClick={() => setReload(true)}
                      className="posts__reload posts__svg"
                    >
                      <Reload />
                    </div>
                  )}
                </div>
              ) : (
                ''
              )}
            </div>
          </div>
        ) : (
          <PostsItemReload
            item={element}
            setElement={setElement}
            setReload={setReload}
            rating={rating}
            setRating={setRating}
          />
        )}
      </>
    );
  }
);

type PostTypes<T = any> = Pick<CommentsConf, 'model'> & T;

type ReviewTypes = {
  deviceId?: any;
  item: TypesConfig;
  fetching: () => void;
};

type ElementType = {
  setElement: React.Dispatch<React.SetStateAction<TypesConfig>>;
  setReload: React.Dispatch<React.SetStateAction<boolean>>;
};

type ElementItemConf = {
  setRating: React.Dispatch<React.SetStateAction<number>>;
  rating: number;
};

export type Updatetypes = Omit<
  ElementType & ReviewTypes & ElementItemConf,
  'fetching'
>;
