import { FieldValues } from 'react-hook-form';
import { AxiosPromise } from 'axios';
import { $host } from '../http/index';

export class useFetching {
  static async getAllBrandOrType(type: number | string, searchParams) {
    const brand: number[] = [];
    const color: number[] = [];
    let sales = false;
    for (const [key, value] of searchParams.entries()) {
      if (key.startsWith('brand')) {
        brand.push(+value);
      } else if (key.startsWith('color')) {
        color.push(+value);
      } else if (key.startsWith('sale')) {
        sales = true;
      }
    }

    const response = await $host.post('api/device/all', {
      brandId: JSON.stringify(brand),
      colorId: JSON.stringify(color),
      typeId: type,
      sales,
      low: searchParams.get('low') || 0,
      tall: searchParams.get('tall') || 1000,
    });

    return response.data;
  }

  static async getAllGoods(typeId: null | number, page: string) {
    const response = await $host.post('api/device/goods', { typeId, page });
    return response.data;
  }

  static async getAll() {
    const response = await $host.get('api/device/');
    return response.data;
  }

  static async getOne(id: number): Promise<any> {
    const { data } = await $host.get('api/device/' + id);
    return data;
  }

  static async getBrands(): Promise<any> {
    try {
      const { data } = await $host.get('api/brand/');
      return data;
    } catch (e) {
      console.log(e);
    }
  }

  static async getColor(): Promise<any> {
    try {
      const { data } = await $host.get('api/color/');
      return data;
    } catch (e) {
      console.log(e);
    }
  }

  static async getTypes(): Promise<any> {
    const { data } = await $host.get('api/category/');
    return data;
  }

  static async delete(id: number): Promise<any> {
    try {
      const { data } = await $host.delete(`api/img/${id}`);

      await $host.delete(`api/device/delete/${id}`);

      return data;
    } catch (e) {
      console.log(e);
    }
  }

  static async setImg(img): Promise<any> {
    try {
      const { data } = await $host.post(
        `api/img`,
        { ...img },
        {
          headers: {
            'Content-Type': 'multipart/form-data',
          },
        }
      );
      return data;
    } catch (e) {
      console.log(e);
    }
  }

  static async change(objdata: FieldValues, id: number, img: string) {
    try {
      await $host.patch(
        `api/img`,
        { id, img, ...objdata.files },
        {
          headers: {
            'Content-Type': 'multipart/form-data',
          },
        }
      );

      await $host.patch(`api/device`, {
        id,
        name: objdata.name,
        price: objdata.price,
        sale: objdata.sale,
        brandId: objdata.brand,
        typeId: objdata.category,
        colorId: objdata.color,
        info: objdata.information,
      });
    } catch (e) {
      console.log(e);
    }
  }

  static async set(objdata, imgId): Promise<any> {
    const { data } = await $host.post(`api/device`, {
      name: objdata.name,
      price: objdata.price,
      sale: objdata.sale,
      brandId: objdata.brand,
      typeId: objdata.category,
      colorId: objdata.color,
      deviceimgId: imgId,
      info: objdata.information,
    });
    return data;
  }
}
