import React, { useContext, useEffect, useState } from 'react';
import { FieldValues, SubmitHandler, useForm } from 'react-hook-form';
import { Context } from '../../../..';
import useLoading from '../../../../Hooks/useLoading';
import { login } from '../../../../http/authAPI';
import { ModalActive } from '../../Modal';
import { TwoFactor } from '../TwoFactor/TwoFactor';
import Body from './components/EnterBody/Body';
import './style/SignIn.scss';
import { validateUserValue } from '../../../../UI/validateUserValue/validateUserValue';
import { loginCheck } from '../../../../http/checkAPI';
import { observer } from 'mobx-react-lite';
import { basketAll } from '../../../../http/basketAPI';
import { FavoriteAPI } from '../../../../http/favoriteAPI';
import axios from 'axios';

const SignIn = observer(() => {
  const {
    register,
    formState: { errors },
    handleSubmit,
    setError,
    setValue,
  } = useForm({ mode: 'onBlur' });
  const [active, setActive] = useContext(ModalActive);
  const [dataForm, setDataForm] = useState({});
  const [pass, setPass] = useState<boolean>(true);
  const [value, setValues] = useState<string>('');
  const [code, setCode] = useState<string>('');
  const { user, basket, favorite } = useContext(Context);
  const [isLoading, fetching] = useLoading(async (data) => {
    if (pass) {
      const response = await loginCheck(data);

      if (response.message == 'Ok') {
        const dataNew = data;
        dataNew.number = response.user.number;
        setDataForm(dataNew);
        setPass(!pass);
      } else {
        validateUserValue(response, setError, true);
      }
    } else {
      if (code == value) {
        const response = await login(dataForm);
        if (response.message == 'Ok') {
          user.setAuth = true;
          user.setUser = response.user;
          Promise.all([basketAll(), FavoriteAPI.getAll()]).then(
            axios.spread((basketData, favoriteData) => {
              basket.setProducts = basketData.devices;
              favorite.setProducts = favoriteData.devices;
            })
          );

          setActive(false);
        } else {
          validateUserValue(response, setError, true);
        }
      }
    }
  });

  useEffect(() => {
    setPass(true);
  }, [active]);

  const onSubmit: SubmitHandler<FieldValues> = (data) => {
    fetching(data);
  };

  return (
    <form className="signin" onSubmit={handleSubmit(onSubmit)}>
      {pass ? (
        <Body
          loading={isLoading}
          setValue={setValue}
          register={register}
          errors={errors}
        />
      ) : (
        <TwoFactor
          code={code}
          dataForm={dataForm}
          setPass={setPass}
          setCode={setCode}
          setValue={setValues}
        />
      )}
    </form>
  );
});

export default SignIn;
