import { $host } from './index';

export class CommentAPI {
  static async getAll(id?: string | number) {
    try {
      const { data } = await $host.get('api/comment/' + id);
      return data;
    } catch (error) {
      console.log(error);
    }
  }

  static async set(text, deviceId): Promise<any> {
    try {
      const { data } = await $host.post(`api/comment`, {
        text,
        deviceId,
      });
      return data;
    } catch (error) {
      console.log(error);
    }
  }

  static async change(id, text): Promise<any> {
    try {
      const { data } = await $host.patch(`api/comment`, {
        id,
        text,
      });
      return data;
    } catch (error) {
      console.log(error);
    }
  }

  static async delete(commentId, userId): Promise<any> {
    try {
      const { data } = await $host.delete(`api/comment/${commentId}/${userId}`);
      return data;
    } catch (error) {
      console.log(error);
    }
  }

  static async setLike(userId: number, commentId: number, add): Promise<any> {
    try {
      const { data } = await $host.post(`api/likecomment`, {
        userId,
        commentId,
        add,
      });
      return data;
    } catch (error) {
      console.log(error);
    }
  }
}
