import { observer } from 'mobx-react-lite';
import { useContext } from 'react';
import { Context } from '../..';
import { FavoriteAPI } from '../../http/favoriteAPI';
import { ReactComponent as Heart } from '../../img/icons/heart.svg';
import './HeartBtn.scss';

const HeartBtn: React.FC<Heart> = observer(({ model, id, one }) => {
  const { user, favorite } = useContext(Context);

  const headerCheck = async () => {
    if (user.getAuth) {
      const response = await FavoriteAPI.app(id);
      favorite.setProducts = response.devices;
    } else {
      if (model) model('in', 'sing');
    }
  };

  if (one) {
    return (
      <div onClick={headerCheck} className="oneproduct__heart">
        <div
          className={
            favorite.getProducts.filter((item) => item.id == id).length &&
            user.getAuth
              ? 'heart add '
              : 'heart '
          }
        >
          <Heart />
        </div>
      </div>
    );
  }

  return (
    <div
      onClick={headerCheck}
      className={
        favorite.getProducts.filter((item) => item.id == id).length &&
        user.getAuth
          ? 'heart add'
          : 'heart'
      }
    >
      <Heart />
    </div>
  );
});

export default HeartBtn;

type Heart = {
  id: number;
  model?: (arg: string, arg2: string) => void;
  one?: boolean;
};
