import { observer } from 'mobx-react-lite';
import { useContext, useEffect, useRef, useState } from 'react';
import { useLocation } from 'react-router-dom';
import { Context } from '../../../..';
import useLoading from '../../../../Hooks/useLoading';
import { CommentAPI } from '../../../../http/commentAPI';
import { RatingAPI } from '../../../../http/RatingAPI';
import Loading, { BtnLoading } from '../../../../UI/Loading/Loading';
import { StarsChange } from '../Start/StarDevice';
import { Updatetypes } from './POSTS';

export const PostsItemReload: React.FC<Updatetypes> = observer(
  ({ item, setElement, setReload, rating, setRating }) => {
    const deviceId = useLocation().pathname.match(/[0-9]*$/gm);
    const { likes, user } = useContext(Context);
    const [value, setValue] = useState(item.text);
    const ref = useRef<HTMLTextAreaElement>(null!);
    const [isLoading, fetching] = useLoading(async () => {
      if (deviceId) {
        const response = await CommentAPI.change(item.id, value);
        const likesData = await RatingAPI.change(
          user.getUser.id,
          deviceId[0],
          rating
        );

        if (likesData.message == 'ok') {
          likes.setlikes = likesData.rating;
          setElement(response);
          setReload(false);
        }
      }
    });
    useEffect(() => {
      ref.current.focus();
    });
    const handlerSubmit = async (e: React.FormEvent) => {
      e.preventDefault();
      fetching();
    };

    return (
      <div className="reload">
        <form onSubmit={handlerSubmit}>
          <div className="reload__rating">
            <StarsChange
              number={rating}
              setRating={setRating}
              text={'Your score on the product'}
            />
          </div>
          <div className="reoad__input">
            <textarea
              ref={ref}
              name="reload"
              value={value}
              onChange={(e) => {
                setValue(e.target.value);
              }}
            />
          </div>
          <div className="reload__footer">
            <button className="reload__btn">
              {isLoading ? (
                <BtnLoading>
                  <Loading />
                </BtnLoading>
              ) : (
                <p className="big">UPDATE REVIEW</p>
              )}
            </button>
            <div className="reload__cancel" onClick={() => setReload(false)}>
              <p className="big">Cancel</p>
            </div>
          </div>
        </form>
      </div>
    );
  }
);
