import { ReactComponent as Heart } from '../../../../../img/icons/heart.svg';
import { ReactComponent as Gear } from '../../../../../img/icons/gear.svg';
import { ReactComponent as Cart } from '../../../../../img/icons/cart.svg';
import { Link, useLocation, useNavigate } from 'react-router-dom';
import { ReactComponent as User } from '../../../../../img/icons/user.svg';
import { observer } from 'mobx-react-lite';
import { useContext } from 'react';
import { Context } from '../../../../..';
import {
  addClassBurger,
  showMenu,
} from '../../../../../components/burger/burger';
import CountHeartOrBasket from './CountHeartOrBasket';

export const ActiveUser = observer(() => {
  const { basket, favorite, user } = useContext(Context);
  const router = useNavigate();

  const pageBasket = (e: React.MouseEvent<HTMLElement>) => {
    if (basket.count > 0) {
      document.documentElement.scrollIntoView(true);
      removeBurger(e);
      if (e.button == 1) {
        window.open('http://localhost:3000/basket', '_blank');
      } else {
        router('basket');
      }
    }
  };

  const goRoof = (e: React.MouseEvent<HTMLElement>) => {
    document.documentElement.scrollIntoView(true);
    removeBurger(e);
  };

  const removeBurger = (e) => {
    showMenu();
    addClassBurger(e);
  };

  return (
    <>
      <Link to="user/profile" className="blockicons__profile " onClick={goRoof}>
        <User />
      </Link>
      {user.getUser?.role == 'Admin' && (
        <Link
          to="admin/goods"
          className="blockicons__profile "
          onClick={goRoof}
        >
          <Gear />
        </Link>
      )}
      <Link
        onClick={goRoof}
        to="/favorites"
        className="blockicons__parentblock heartIcon"
      >
        <div className="blockicons__heart">
          <Heart />
        </div>
        <CountHeartOrBasket count={favorite.count} />
      </Link>

      <div
        onMouseDown={(e) => pageBasket(e)}
        className="blockicons__parentblock"
      >
        <div className="blockicons__basket">
          <Cart />
        </div>
        <CountHeartOrBasket count={basket.count} />
      </div>
    </>
  );
});
