import { observer } from 'mobx-react-lite';
import React, { useEffect, useState } from 'react';
import NumberCode from './Components/NumberCode/NumberCode';
import Timer from './Components/Timer/Timer';
import './style/TwoFactor.scss';
import useLoading from '../../../../Hooks/useLoading';
import { Button } from '../../../../UI/Button/Button';
import { TwoFactorTypes } from '../../../../interface/ui.interface';
import { validcode } from '../../../../http/checkAPI';
import Loading, { BtnLoading } from '../../../../UI/Loading/Loading';

export const TwoFactor = observer(
  ({ dataForm, setPass, setValue, setCode, code }: TwoFactorTypes) => {
    const [times, setTimes] = useState<number>(15);
    const [isLoading, fetching] = useLoading(async (): Promise<void> => {
      const code = await validcode();
      setCode(code);
      alert(code);
    });

    const back = () => {
      setPass(() => true);
    };

    useEffect(() => {
      if (code == '') fetching();
    }, []);

    const requestNumber = () => {
      fetching();
      setTimes(15);
    };

    return (
      <div className="twofactor in">
        <h3 className="twofactor__title">Confirm Login</h3>
        <div className="twofactor__text">
          <p className="">
            {` To the number ${dataForm.number} Sent an SMS with a 4-digit confirmation code`}
          </p>
        </div>
        <Timer
          times={times}
          setTimes={setTimes}
          requestNumber={requestNumber}
        />
        <NumberCode setValue={setValue} />

        <div className="twofactor__footer">
          <p onClick={back} className="mini twofactor__back">
            {'< Back'}
          </p>
          <div className="twofactor__btn">
            {isLoading ? (
              <BtnLoading>
                <Loading />
              </BtnLoading>
            ) : (
              <Button name="sign">
                <p className="mini">Next</p>
              </Button>
            )}
          </div>
        </div>
      </div>
    );
  }
);
