import BlockGym from './BlockGym';
import ScheduleBanner from './ScheduleBanner/ScheduleBanner';
import { useContext, useEffect } from 'react';
import { ModalContext } from '../../App';
import './style/Schedule.scss';

export default function Schedule() {
  const { active, type } = useContext(ModalContext);
  const renderModal = (name: string, typeblock: string) => {
    active((active: boolean) => !active);
    type.name(name);
    type.type(typeblock);
  };

  useEffect(() => {
    document.title = `Schedule`;
  }, []);
  return (
    <>
      <ScheduleBanner fun={renderModal} />
      <div className="schedule indent_last">
        <div className="container">
          <div className="blockvalue">
            <div className="blockvalue__one elem">
              <div className="block">
                <BlockGym
                  number="one"
                  block="itemone"
                  title="Kids workout"
                  one={'2 am'}
                  two={'1.h'}
                  three={'15'}
                  fun={renderModal}
                />
              </div>
              <div className="daysblock one">
                <div className="daysblock__body">
                  <p className="mini">Mon - Sat</p>
                </div>
              </div>
            </div>

            <div className="blockvalue__two elem ">
              <div className="daysblock two">
                <div className="daysblock__body">
                  <p className="mini">Mon - Sat</p>
                </div>
              </div>
              <div className="block">
                <BlockGym
                  number="two"
                  block="itemtwo"
                  title="Group training"
                  one={'7.30 pm'}
                  two={'2.h'}
                  three={'20'}
                  fun={renderModal}
                />
              </div>
            </div>
          </div>

          <div className="blockvalue">
            <div className="blockvalue__one elem">
              <div className="block">
                <BlockGym
                  number="three"
                  block="itemone"
                  title="CrossFit"
                  one={'10 am'}
                  two={'2.h'}
                  three={'15'}
                  fun={renderModal}
                />
              </div>
              <div className="daysblock one">
                <div className="daysblock__body">
                  <p className="mini">Mon - Fri</p>
                </div>
              </div>
            </div>

            <div className="blockvalue__two elem ">
              <div className="daysblock two">
                <div className="daysblock__body">
                  <p className="mini">Sat, Sun</p>
                </div>
              </div>
              <div className="block">
                <BlockGym
                  number="four"
                  block="itemtwo"
                  title="Sparring"
                  one={'10 am'}
                  two={'3.h'}
                  three={'15'}
                  fun={renderModal}
                />
              </div>
            </div>
          </div>

          <div className="blockvalue">
            <div className="blockvalue__one elem">
              <div className="block">
                <BlockGym
                  number="five"
                  block="itemone"
                  title="Competition"
                  one={'5 am'}
                  two={'2-3.h'}
                  three={''}
                  fun={renderModal}
                />
              </div>
              <div className="daysblock one last">
                <div className="daysblock__body">
                  <p className="mini">
                    Once in three <br />
                    months
                  </p>
                </div>
              </div>
            </div>

            <div className="blockvalue__two  elem">
              <div className="daysblock two last">
                <div className="daysblock__body">
                  <p className="mini">Mon - Sat</p>
                </div>
              </div>
              <div className="block">
                <BlockGym
                  number="six"
                  block="itemtwo"
                  title="Individual"
                  one={'3 am'}
                  two={'1-2.h'}
                  three={'1'}
                  fun={renderModal}
                />
              </div>
            </div>
          </div>
        </div>
      </div>
    </>
  );
}
