import React, { useContext, useEffect } from 'react';
import { ReactComponent as Sun } from '../../../../img/icons/sun.svg';
import { ReactComponent as Moon } from '../../../../img/icons/moon.svg';
import './style/BlockIcons.scss';
import { Context } from '../../../..';
import Sign from './components/Sign';
import { ActiveUser } from './components/ActiveUser';
import { $ } from '../../../../components/customeJQ/customeJQ';
import { ModalContext } from '../../../../App';
import { observer } from 'mobx-react-lite';
import { lodingDOM } from '../../../../components/loadWin/loadWin';

export const BlockIcons = observer(() => {
  const changeTheme = (e: React.MouseEvent<HTMLElement>) => {
    const target = (
      (e.target as HTMLElement).closest('.blocktheme') as HTMLElement
    ).dataset.theme as string;
    let color = 'false';

    if (target) {
      if (target == 'dark') {
        color = 'light';
      } else {
        color = 'dark';
      }
    }

    const date = new Date();

    const theme = {
      value: color,
      timestamp: date.setDate(date.getDate() + 1),
    };
    localStorage.setItem('theme', JSON.stringify(theme));

    lodingDOM(theme);
  };

  const { user } = useContext(Context);
  const { active, type } = useContext(ModalContext);
  const renderModal = (
    e: React.MouseEvent<HTMLElement>,
    name: string,
    typeblock: string
  ) => {
    active((active: boolean) => !active);
    $('.header__menu, .header__burger').remove('show');
    $('.goroof').remove('show');
    type.name(name);
    type.type(typeblock);
  };

  useEffect(() => {
    let theme = JSON.parse(localStorage.getItem('theme') || 'false');
    if (
      theme &&
      new Date(theme.timestamp).toLocaleDateString() <
        new Date().toLocaleDateString()
    ) {
      theme = localStorage.setItem('theme', 'false');
    }
    lodingDOM(theme);
  });

  return (
    <div className={user.getAuth ? 'blockicons active' : 'blockicons '}>
      <div className="blockicons__items">
        <div className="blockicons__elements">
          <div onClick={changeTheme} className="blockicons__theme">
            <div>
              <div data-theme="sun" className="blockicons__sun blocktheme">
                <Sun />
              </div>
              <div data-theme="dark" className="blockicons__moon blocktheme">
                <Moon />
              </div>
            </div>
          </div>

          {user.getAuth ? <ActiveUser /> : <Sign renderModal={renderModal} />}
        </div>
      </div>
    </div>
  );
});
