import { BtnArrow } from '../../UI/Button/Button';
import Paragraph from '../../UI/Paragraph/Paragraph';

export default function BlockGym({
  number,
  fun,
  title,
  block,
  one,
  two,
  three,
}) {
  return (
    <div
      data-gym="true"
      className={`scroll blockvalue__${block} item ${number}`}
    >
      <div className="item__roof">
        <div className="item__hour">
          <div>
            <p className="mini">
              <span translate="no">{one}</span>
              <br />
              Start
            </p>
            <p className="mini">
              <span translate="no">{two}</span>
              <br />
              duration
            </p>
            <p className="mini">
              <span>{three}</span>
              <br /> people
            </p>
          </div>
        </div>
        <div className="item__img">
          <div className="item__blockfree">
            <div className="item__text">
              <Paragraph
                name="mini"
                ru=" "
                en="Try a free class"
              />
            </div>
            <BtnArrow name="old" onClick={() => fun('free', 'other')}>
              <span className="en">Enroll</span>
              <span className="ru"></span>
            </BtnArrow>
          </div>
        </div>
      </div>
      <div className="item__body">
        <h3>{title}</h3>
        <p className="mini">Lorem ipsum dolor sit amet.</p>
      </div>
    </div>
  );
}
