import { makeAutoObservable } from 'mobx';
import { FavoriteConf } from './GlobalState.config';

class Favorite {
  private products: FavoriteConf[];
  private productsLimit: FavoriteConf[];

  constructor() {
    this.products = [];
    this.productsLimit = [];
    makeAutoObservable(this);
  }

  get getProducts() {
    return this.products;
  }
  get getProductsLimit() {
    return this.productsLimit;
  }

  get count() {
    return this.products.length;
  }

  set setProducts(products: FavoriteConf[]) {
    this.products = products;
  }

  set setLimitProduct(arg: FavoriteConf[]) {
    this.productsLimit = arg;
  }

  clear() {
    this.products = [];
  }
}

export default Favorite;
