import React, { useContext, useEffect } from 'react';
import { Logo } from './conmopents/Logo/Logo';
import { NavBlock } from './conmopents/NavBlock/NavBlock';
import './style/Header.scss';
import './style/BreakPoints.scss';
import { addClassBurger, showMenu } from '../../components/burger/burger';
import { HeaderMenu } from './conmopents/HeaderMenu/HeaderMenu';
import { ReactComponent as Phone } from '../../img/icons/phone.svg';
import { ReactComponent as Calen } from '../../img/icons/calendar.svg';
import { $ } from '../../components/customeJQ/customeJQ';
import { ModalContext } from '../../App';
import Paragraph from '../../UI/Paragraph/Paragraph';
import { useLocation } from 'react-router-dom';
import { BlockIcons } from './conmopents/BlockIcons/BlockIcons';

export default function Header() {
  const clickBurger = (e) => {
    (document.querySelector('.menu__body') as HTMLElement)?.scrollIntoView({
      block: 'start',
      behavior: 'auto',
    });
    $('.goroof').remove('show');
    addClassBurger(e);
  };

  const router = useLocation();

  const { active, type } = useContext(ModalContext);
  const renderModal = (name: string, typeblock: string) => {
    active((active: boolean) => !active);
    type.name(name);
    type.type(typeblock);
  };

  useEffect(() => {
    showMenu();
  }, []);

  useEffect(() => {
    window.addEventListener('scroll', onScroll);
    return () => {
      window.removeEventListener('scroll', onScroll);
    };
  });

  return (
    <>
      <div className="phone lock-padding">
        <div className="container">
          <div className="phone__contain">
            <a href="tel:+19992059331" className="phone__body">
              <div className="phone__icon">
                <Phone />
              </div>
              <div className="phone__text">
                <Paragraph name="mini" ru="" en="Contact us" />
              </div>
            </a>
            <div className="enroll">
              <div
                onClick={() => renderModal('free', 'other')}
                className="enroll__body"
              >
                <div className="enroll__icons">
                  <Calen />
                </div>
                <div className="enroll__text">
                  <Paragraph
                    name="mini"
                    ru=""
                    en="Enroll first training"
                  />
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
      <header
        className={
          router.pathname == '/'
            ? 'header home lock-padding'
            : 'header lock-padding'
        }
      >
        <HeaderMenu />
        <div className="header__raw">
          <div className="container">
            <div className="header__wrap">
              <div onClick={clickBurger} className="header__burger">
                <span></span>
              </div>
              <Logo />
              <NavBlock />
              <BlockIcons />
            </div>
          </div>
          <div className="header__line">
            <div className="header__linesecond"></div>
          </div>
        </div>
      </header>
    </>
  );
}

let lastScrollTop = 0;
function onScroll() {
  const scroll = window.pageYOffset + document.documentElement.clientHeight;
  if (window.pageYOffset > 100) {
    $('.header, .phone').add('show');
  } else {
    $('.header, .phone').remove('show');
  }

  if (window.pageYOffset > 200 && window.pageYOffset > lastScrollTop) {
    $('.header').add('deactive');
  } else {
    $('.header').remove('deactive');
  }

  if (scroll >= window.innerHeight / 2 - 300) {
    $('.bannerloc__container').add('deactive');
  } else {
    $('.bannerloc__container').remove('deactive');
  }

  const elements = Array.from(document.querySelectorAll('.scroll'));

  for (const item of elements) {
    if ((item as HTMLElement)?.dataset.block) {
      if (scroll > item.getBoundingClientRect().top + window.pageYOffset) {
        item.classList.add('show');
      }
    } else if ((item as HTMLElement)?.dataset.gym) {
      if (scroll > item.getBoundingClientRect().top + window.pageYOffset) {
        item?.closest('.elem')?.classList.add('show');
      }
    } else if ((item as HTMLElement)?.dataset.footer) {
      const arrowgo = document.querySelector('.goroof') as HTMLElement;
      if (scroll >= item.getBoundingClientRect().bottom + window.pageYOffset) {
        arrowgo?.classList.add('show');
      } else {
        arrowgo?.classList.remove('show');
      }
      const arrowgotwo = document.querySelector('.footer') as HTMLElement;
      if (
        scroll >=
        item.getBoundingClientRect().bottom + window.pageYOffset + 20
      ) {
        arrowgotwo?.classList.add('show');
      } else {
        arrowgotwo?.classList.remove('show');
      }
    } else if ((item as HTMLElement)?.dataset.sale) {
      const arrowgo = document.querySelector('.basket__price') as HTMLElement;
      const blockone = document.querySelector(
        '.productbasket__goods'
      ) as HTMLElement;
      const blocktwo = arrowgo.getBoundingClientRect();
      const block = (
        document.querySelector('.pageonebas') as HTMLElement
      ).getBoundingClientRect();
      const scrolltop = window.pageYOffset;

      if (
        arrowgo.clientHeight >= blockone.clientHeight ||
        document.documentElement.clientWidth < 1420
      ) {
        arrowgo?.classList.remove('bottom');
        arrowgo?.classList.remove('show');
        arrowgo?.classList.remove('showtwo');
      } else if (
        scrolltop >= item.getBoundingClientRect().top + window.pageYOffset &&
        scrolltop >= lastScrollTop &&
        block.bottom + window.pageYOffset > blocktwo.bottom + window.pageYOffset
      ) {
        arrowgo?.classList.add('show');
        arrowgo?.classList.remove('showtwo');
      } else if (
        scrolltop >= item.getBoundingClientRect().top + window.pageYOffset &&
        blocktwo.top >= 0 &&
        scrolltop < lastScrollTop &&
        block.bottom + window.pageYOffset <=
          blocktwo.bottom + window.pageYOffset
      ) {
        arrowgo?.classList.remove('bottom');
        arrowgo?.classList.add('show');
        arrowgo?.classList.add('showtwo');
      } else if (
        block.bottom + window.pageYOffset <=
        blocktwo.bottom + window.pageYOffset
      ) {
        arrowgo?.classList.add('bottom');
        arrowgo?.classList.remove('show');
      } else if (
        scrolltop <= lastScrollTop &&
        scrolltop <= block.top + window.pageYOffset
      ) {
        arrowgo?.classList.remove('bottom');
        arrowgo?.classList.remove('show');
        arrowgo?.classList.remove('showtwo');
      }
    }
  }

  lastScrollTop = window.pageYOffset;
}
