import { observer } from 'mobx-react-lite';
import { Pagination } from 'swiper';

import { Swiper, SwiperSlide } from 'swiper/react';
import { API_URL } from '../../../../../components/Routers/links';
import { DeviceConfigAll } from '../../../../../mobx/GlobalState.config';

import 'swiper/css';
import './Slider.scss';
import 'swiper/css/pagination';

export const Slider: React.FC<SliderConf> = observer(({ product }) => {
  const pagination = {
    clickable: true,
  };

  return (
    <>
      <Swiper
        observer={true}
        observeParents={true}
        pagination={pagination}
        modules={[Pagination]}
        className={
          Array.from(Object.values(JSON.parse(product?.deviceimg.img)))
            .length <= 1
            ? 'SwiperProduct notpagination'
            : 'SwiperProduct'
        }
        allowTouchMove={true}
      >
        {Array.from(Object.values(JSON.parse(product?.deviceimg.img))).map(
          (item, index) => {
            return (
              <SwiperSlide key={index}>
                <div
                  className="product__img "
                  style={{
                    backgroundImage: `url(${API_URL}${item})`,
                  }}
                ></div>
              </SwiperSlide>
            );
          }
        )}
      </Swiper>
    </>
  );
});

type SliderConf = {
  product: DeviceConfigAll;
};
