import { btnHOC, ProductBtn } from '../../../../../UI/ProductBtn/ProductBtn';
import React, { useContext } from 'react';
import { observer } from 'mobx-react-lite';
import { ModalContext } from '../../../../../App';
import { Link, useNavigate } from 'react-router-dom';
import { DeviceConfigAll } from '../../../../../mobx/GlobalState.config';
import HeartBtn from '../../../../../UI/HeartBtn/HeartBtn';
import { Slider } from './Slider';
import { ReactComponent as Star } from '../../../../../img/icons/star.svg';

interface TypeBlock {
  product: DeviceConfigAll;
}

export const ItemProduct: React.FC<TypeBlock> = observer(({ product }) => {
  const { active, type } =
    useContext<React.ContextType<typeof ModalContext>>(ModalContext);
  const Btn = btnHOC(ProductBtn);

  const renderModal = (name: string, typeblock: string) => {
    active((active: boolean) => !active);
    type.name(name);
    type.type(typeblock);
  };

  return (
    <div className="product__item">
      <div className="product__blockimage">
        <Slider product={product} />
        <div className="product__heart">
          <HeartBtn id={product.id} model={renderModal} />
        </div>
        {product.sale > 0 && (
          <div className="product__sale">
            <div className="product__saleback">
              <div className="product__saleground"></div>
              <div className="product__saletext">
                <p className="mini">-{product.sale}%</p>
              </div>
            </div>
          </div>
        )}

        <div className="product__rating rating">
          <div className="rating__body">
            <div className="rating__star">
              <Star />
            </div>
            <div className="rating__text">
              <p className="mini">{countLikes(product.ratings) || 0}</p>
            </div>
          </div>
        </div>
      </div>
      <div className="product__text">
        <div
          className={product.sale > 0 ? 'product__price add' : 'product__price'}
        >
          <SaleProduct product={product} />
        </div>
        <div className="product__textbottom">
          <div translate="no" className="product__name">
            <p className="">
              <span data-type-string>
                {product.name.length <= 30
                  ? product.name
                  : product.name
                      .split('')
                      .filter((item, index) => index <= 30)
                      .join('') + '...'}{' '}
                /{' '}
              </span>

              <span className="product__category ">{product?.brand?.name}</span>
            </p>
          </div>
        </div>
      </div>
      <Link
        to={`/shop/item/${product.id}`}
        onClick={() => {
          document.documentElement.scrollIntoView(true);
        }}
        className="product__viewmore"
      >
        <p className="big">review</p>
      </Link>
      <Btn product={product} model={renderModal} />
    </div>
  );
});

export const SaleProduct: React.FC<TypeBlock> = observer(({ product }) => {
  return (
    <>
      {product.sale > 0 ? (
        <>
          <p className="big" data-type-number>
            $
            {(+product.price - (+product.price / 100) * product.sale).toFixed(
              1
            )}
          </p>
          <p className="mini" data-type-number>
            ${product.price}
          </p>
        </>
      ) : (
        <>
          <p className="big" data-type-number>
            ${product.price}
          </p>
        </>
      )}
    </>
  );
});

export const countLikes = (product) => {
  if (product.length > 0) {
    let five = 0;
    let four = 0;
    let three = 0;
    let two = 0;
    let one = 0;

    product.map((item) => {
      switch (item.quantity) {
        case 5:
          five += 1;
          break;
        case 4:
          four += 1;
          break;
        case 3:
          three += 1;
          break;
        case 2:
          two += 1;
          break;
        case 1:
          one += 1;
          break;
      }
    });

    return +(
      (five * 5 + four * 4 + three * 3 + two * 2 + one) /
      product.length
    ).toFixed(0);
  }
};
