import jwtDecode from 'jwt-decode';
import { useContext, useEffect, useState } from 'react';
import { FieldValues, SubmitHandler, useForm } from 'react-hook-form';
import { Context } from '../../../..';
import useLoading from '../../../../Hooks/useLoading';
import { registration } from '../../../../http/authAPI';
import { regCheck } from '../../../../http/checkAPI';
import Loading from '../../../../UI/Loading/Loading';
import { validateUserValue } from '../../../../UI/validateUserValue/validateUserValue';
import { ModalActive } from '../../Modal';
import { TwoFactor } from '../TwoFactor/TwoFactor';
import Body from './components/UpBody/Body';
import './style/SignUp.scss';

const SignUp: React.FC = () => {
  const {
    register,
    formState: { errors },
    handleSubmit,
    getValues,
    setValue,
    setError,
  } = useForm({ mode: 'onBlur' });
  const [dataForm, setDataForm] = useState({});
  const [active, setActive] = useContext(ModalActive);
  const [page, setPage] = useState<string>('first');
  const [pass, setPass] = useState<boolean>(true);
  const [value, setValues] = useState<string>('');
  const [code, setCode] = useState<string>('');
  const { user } = useContext(Context);
  const [isLoading, fetching] = useLoading(async (data) => {
    if (pass) {
      const response = await regCheck(data);
      if (response.message == 'Ok') {
        const dataNew = data[0];
        setDataForm(dataNew);
        setPass(!pass);
      } else {
        validateUserValue(response, setError, false, setPage);
      }
    } else {
      if (code == value) {
        const response = await registration(dataForm);

        if (response.message == 'Ok') {
          user.setAuth = true;
          user.setUser = dataForm;
          setActive(false);
        } else {
          validateUserValue(response, setError, false, setPage);
        }
      }
    }
  });

  useEffect(() => {
    setPass(true);
  }, [active]);

  const onSubmit: SubmitHandler<FieldValues> = (data) => {
    if (page != 'first') {
      fetching(data);
    } else {
      setPage('second');
    }
  };

  return (
    <form className="signup" onSubmit={handleSubmit(onSubmit)}>
      {isLoading ? (
        <Loading />
      ) : (
        <>
          {pass ? (
            <Body
              setValue={setValue}
              register={register}
              errors={errors}
              getValues={getValues}
              page={page}
              setPage={setPage}
              loading={isLoading}
            />
          ) : (
            <TwoFactor
              code={code}
              dataForm={dataForm}
              setPass={setPass}
              setCode={setCode}
              setValue={setValues}
            />
          )}
        </>
      )}
    </form>
  );
};

export default SignUp;
