import 'react-app-polyfill/ie11';
import 'react-app-polyfill/stable';

import React, { createContext } from 'react';
import ReactDOM from 'react-dom/client';
import { BrowserRouter } from 'react-router-dom';
import App from './App';
import BasketStore from './mobx/BasketStore';
import Comment from './mobx/Comment';
import Device from './mobx/Device';
import DeviceChange from './mobx/DeviceChange';
import Favorite from './mobx/Favorite';
import Likes from './mobx/Likes';
import Tuner from './mobx/Tuner';
import User from './mobx/User';
import './style/zero.scss';

export const Context = createContext<UserDeviceType>(null!);

const root = ReactDOM.createRoot(
  document.getElementById('root') as HTMLElement
);
root.render(
  <Context.Provider
    value={{
      user: new User(),
      device: new Device(),
      basket: new BasketStore(),
      favorite: new Favorite(),
      tuner: new Tuner(),
      comment: new Comment(),
      likes: new Likes(),
      product: new DeviceChange(),
    }}
  >
    <React.StrictMode>
      <BrowserRouter>
        <App />
      </BrowserRouter>
    </React.StrictMode>
  </Context.Provider>
);

export interface UserDeviceType {
  tuner: Tuner;
  user: User;
  device: Device;
  basket: BasketStore;
  favorite: Favorite;
  comment: Comment;
  likes: Likes;
  product: DeviceChange;
}
