import { observer } from 'mobx-react-lite';
import { useState, useEffect } from 'react';
import { $ } from '../../components/customeJQ/customeJQ';
import Loading from '../Loading/Loading';
import './WinLoad.scss';
export const LoadingWin: React.FC<WinLoadT> = observer(
  ({ children, isLoading }) => {
    const [winload, setwinload] = useState(true);

    const handlerload = (event) => {
      if (document.readyState == 'complete') {
        setwinload(false);
        $('body').remove('lock');
      } else {
        $('body').add('lock');
        setwinload(true);
      }
    };

    useEffect(() => {
      if (document.readyState == 'complete') {
        setwinload(false);
        $('body').remove('lock');
      }
      document.addEventListener('readystatechange', handlerload);

      return () => {
        document.removeEventListener('readystatechange', handlerload);
      };
    }, []);

    return (
      <>
        <div className={winload || isLoading ? 'winload add' : 'winload '}>
          <Loading name="all" />
        </div>
        {children}
      </>
    );
  }
);

type WinLoadT = {
  children: React.ReactNode;
  isLoading: boolean;
};
