import { Button } from '../../UI/Button/Button';
import { useContext } from 'react';
import { ModalContext } from '../../App';
import './style/Footer.scss';
import { useLocation } from 'react-router-dom';
import { useEffect } from 'react';
import { $ } from '../../components/customeJQ/customeJQ';

export default function FooterDeskont() {
  const { active, type } = useContext(ModalContext);
  const renderModal = (name: string, typeblock: string) => {
    active((active: boolean) => !active);
    type.name(name);
    type.type(typeblock);
  };

  const path = useLocation().pathname;

  useEffect(() => {
    if (/admin|user|price|schedule|contacts/.test(path)) {
      $('.bannershop').add('none');
    } else {
      $('.bannershop').remove('none');
    }
  });

  return (
    <div className={`bannershop scroll`} data-sell>
      <div className="container">
        <div className="bannershop__body">
          <div className="bannershop__title">
            <h1>
              get a discount on your
              <br /> first order
            </h1>
          </div>

          <div className="bannershop__btn">
            <Button name="all" onClick={() => renderModal('discount', 'other')}>
              <p className="mini">get a discount</p>
            </Button>
          </div>
        </div>
      </div>
    </div>
  );
}
