import { $host } from './index';

export class FavoriteAPI {
  static async getAll(limit?: string) {
    try {
      const { data } = await $host.get('api/favorite/', {
        params: {
          _limit: limit,
        },
      });
      return data;
    } catch (error) {
      console.log(error);
    }
  }

  static async app(id: number): Promise<any> {
    try {
      const { data } = await $host.put(`api/favorite/product/${id}/append`);
      return data;
    } catch (error) {
      console.log(error);
    }
  }

  static async clear(): Promise<any> {
    try {
      const { data } = await $host.put(`api/favorite/clear`);
      return data;
    } catch (error) {
      console.log(error);
    }
  }
}
