import React from 'react';
import { Navigate } from 'react-router-dom';
import {
  PAGEITEM,
  GYM,
  PRICE,
  SHOP_ROUTER,
  LIKE,
  SCHEDULE,
  BASKET_ROUTER,
  PAGEITEBAS,
  ABOUTUS,
  USER,
  USERPROFILE,
  ADMINGOODS,
  ADMINUSERS,
  USERPASS,
  ADMINGOODSADD,
  ADMINITEM,
} from './links';
import AbountUs from '../../Pages/AbountUs/AbountUs';
import Contacts from '../../Pages/AbountUs/Contacts';
import { OneProduct } from '../../Pages/ItemPage/OneProduct';
import { Price } from '../../Pages/Price/Price';
import Schedule from '../../Pages/Schedule/Schedule';
const Shop = React.lazy(() => import('../../Pages/Shop/Shop'));
const LikesGoods = React.lazy(
  () => import('../../Pages/LikesGoods/LikesGoods')
);
const Basket = React.lazy(() => import('../../Pages/Basket/Basket'));
const User = React.lazy(() => import('../../Pages/User/User'));

export const publicRoutes = [
  { path: '*', element: <Navigate to="/" replace /> },
  { path: BASKET_ROUTER, element: <Basket /> },
  { path: SHOP_ROUTER, element: <Shop /> },
  { path: SCHEDULE, element: <Schedule /> },
  { path: PAGEITEM, element: <OneProduct /> },
  { path: PAGEITEBAS, element: <OneProduct /> },
  { path: PRICE, element: <Price /> },
  { path: GYM, element: <Contacts /> },
  { path: ABOUTUS, element: <AbountUs /> },
  { path: LIKE, element: <LikesGoods /> },
  { path: USER, element: <User /> },
  { path: ADMINGOODS, element: <User /> },
  { path: ADMINGOODSADD, element: <User /> },
  { path: ADMINITEM, element: <User /> },
  { path: USERPROFILE, element: <User /> },
  { path: ADMINUSERS, element: <User /> },
  { path: USERPASS, element: <User /> },
];
