import { useNavigate } from 'react-router-dom';
import { Button } from '../../../UI/Button/Button';
import GymAdress from './GymAdress';

import './style/Gym.scss';

export default function Gym({ arr }) {
  const goroof = (e: React.MouseEvent) => {
    e.preventDefault();
    document.documentElement.scrollTo({
      top: document.querySelector('.gym')?.getBoundingClientRect().bottom,
      behavior: 'smooth',
    });
  };

  const nav = useNavigate();
  return (
    <div className="gym">
      <div className="gym__wrap">
        <div className="gym__blockone">
          <div className="container">
            <div className="gym__onewrap	">
              <div className="gym__pathblock">
                <div className="gym__path">
                  <p onClick={() => nav('/')} className="mini">
                    Home
                  </p>
                </div>
                <div className="gym__path">
                  <p onClick={() => nav('/contacts')} className="mini">
                    Contacts
                  </p>
                </div>
                <div className="gym__path">
                  <p className="mini">{arr.name}</p>
                </div>
              </div>
              <div className="gym__onetitle">
                <h2>Fight gym on the {arr.adress}</h2>
              </div>
              <div className="gym__btnblock">
                <div className="gym__btn">
                  <Button name="gymbtn" onClick={() => nav('/price')}>
                    <p className="mini">choose a card</p>
                  </Button>
                </div>
                <div className="gym__btn">
                  <Button onClick={goroof} name="other">
                    <p className="mini">more</p>
                  </Button>
                </div>
              </div>
            </div>
          </div>
          <div
            className="gym__oneimage"
            style={{
              backgroundImage: `url(${process.env.PUBLIC_URL}/contacts/${arr.image})`,
            }}
          >
            <div className="gym__oneback"></div>
          </div>
        </div>
        <GymAdress arr={arr} />
      </div>
    </div>
  );
}
