import React from 'react';
import BodyLockorUn from '../../components/BodyLockorUn/BodyLockorUn';
import { ModalInterface } from '../../interface/ui.interface';
import OfferBlock from './OfferBlock';
import SignBlock from './SignBlock';
import './style/Modal.scss';

export const ModalActive = React.createContext<null | any>(null);

const Modal: React.FC<ModalInterface> = ({ active, setActive, sign, type }) => {
  const classActive = active ? 'modal active' : 'modal';

  active ? BodyLockorUn.bodyLock() : BodyLockorUn.bodyUnLock();

  const arr: [
    active: boolean,
    setActive: React.Dispatch<React.SetStateAction<boolean>>
  ] = [active, setActive];

  return (
    <ModalActive.Provider value={arr}>
      <div className={active ? 'modal__back active' : 'modal__back'}></div>
      <div className={classActive} onMouseDown={() => setActive(false)}>
        <div className="modal__bg">
          <div className={`modal__container ${type}`}>
            <div
              className={`modal__body `}
              onMouseDown={(e) => e.stopPropagation()}
            >
              {type == 'other' ? (
                <OfferBlock setActive={setActive} sign={sign} />
              ) : (
                <SignBlock setActive={setActive} sign={sign} />
              )}
            </div>
          </div>
        </div>
      </div>
    </ModalActive.Provider>
  );
};

export default Modal;
