import { useEffect } from 'react';
import './style/Program.scss';

export default function Program({ arr }) {
  useEffect(() => {
    window.addEventListener('scroll', scrolling);
    return () => {
      window.removeEventListener('scroll', scrolling);
    };
  });

  return (
    <div className="program indent">
      <div className="indent"></div>
      <div className="program__wrap">
        <div className="container">
          <div className="program__row">
            <div className="program__blockone">
              <div className="program__blockimg">
                <div className="program__back">
                  <div className="program__img">
                    <div className="program__title img">
                      <div className="program__halftitle img h">
                        <h1 translate="no">
                          Our Gym
                          <br />
                          in the <br /> {arr.subname}
                        </h1>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
              <div className="program__body">
                <div className="program__title">
                  <div className="program__halftitle two h">
                    <h1 translate="no">
                      Our Gym
                      <br />
                      in the <br /> {arr.subname}
                    </h1>
                  </div>
                </div>
                <div className="program__aboutus">
                  <div className="program__text">
                    <p>
                      Lorem ipsum dolor sit amet consectetur adipisicing elit.
                      Ut, sit voluptate temporibus obcaecati ex praesentium
                      fuga, iure eum molestiae saepe tempore maiores ab nemo
                      maxime unde ipsa quasi porro. Minus. Lorem ipsum dolor sit
                      amet consectetur adipisicing elit. Dicta maxime sequi
                    </p>
                  </div>
                </div>
              </div>
            </div>
            <div className="program__blocktwo">
              <div className="program__blockimg ">
                <div className="program__back">
                  <div className="program__img two"></div>
                </div>
              </div>
              <div className="program__blockimg ">
                <div className="program__back">
                  <div className="program__img three"></div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}

function scrolling() {
  const scroll = window.pageYOffset + window.innerHeight;
  const block = (
    document.querySelector('.program__wrap') as HTMLElement
  ).getBoundingClientRect();
  const block1 = Array.from(document.querySelectorAll('.program__halftitle'));

  for (const item of block1) {
    if (
      scroll > block.top + window.pageYOffset &&
      scroll < block.bottom + window.pageYOffset &&
      window.innerWidth > 770
    ) {
      (item as HTMLElement).style.transform = `translate(${
        (scroll - window.innerHeight) * 0.04
      }px)`;
    } else {
      (item as HTMLElement).style.transform = `matrix(1, 0, 0, 1, 0, 0);`;
    }
  }
}
