import { FieldValues, UseFormSetError } from 'react-hook-form';

export const validateUserValue = (
  response: any,
  setError: UseFormSetError<FieldValues>,
  login: boolean,
  setPage?: React.Dispatch<React.SetStateAction<string>>
) => {
  if (!login) {
    if (/email/gi.test(response.message)) {
      setError('email', {
        type: 'custom',
        message: response.message,
      });
      if (setPage) {
        setPage('first');
      }
    } else if (/number/gi.test(response.message)) {
      setError('number', {
        type: 'custom',
        message: response.message,
      });
    } else if (/login/gi.test(response.message)) {
      setError('login', {
        type: 'custom',
        message: response.message,
      });
      if (setPage) {
        setPage('first');
      }
    } else {
      alert(response.message);
    }
  } else if (login) {
    if (/email/gi.test(response.message)) {
      setError('email', {
        type: 'custom',
        message: response.message,
      });
    } else if (/password/gi.test(response.message)) {
      setError('password', {
        type: 'custom',
        message: response.message,
      });
    } else {
      alert(response.message);
    }
  }
};
