import { useNavigate, useParams } from 'react-router-dom';
import { useState, useEffect, useContext } from 'react';
import useLoading from '../../Hooks/useLoading';
import { ModalContext } from '../../App';
import './style/OneProduct.scss';
import { btnHOC, ProductBtn } from '../../UI/ProductBtn/ProductBtn';
import { useFetching } from '../../http/shopAPI';
import { DeviceConfigAll } from '../../mobx/GlobalState.config';
import HeartBtn from '../../UI/HeartBtn/HeartBtn';
import SliderApp from './SliderApp';
import { Comments } from './Comments';
import { StarDevice } from './conmopents/Start/StarDevice';
import { Context } from '../..';
import { SaleProduct } from '../Shop/Goods/components/ItemProduct/ItemProduct';
import { LoadingWin } from '../../UI/WinLoad/WinLoad';

const OneProduct: React.FC = () => {
  const { active, type } = useContext(ModalContext);
  const [elem, setElem] = useState('DETAILS');

  const { likes } = useContext(Context);
  const path = useParams().id;
  const router = useNavigate();
  const [product, setProduct] = useState<DeviceConfigAll>();
  const Btn = btnHOC(ProductBtn);
  const [isLoading, fetching] = useLoading(async () => {
    if (path) {
      const response = await useFetching.getOne(+path);
      setProduct(response);
      likes.setlikes = response.ratings;
    }
  });

  const renderModal = (name: string, typeblock: string) => {
    active((active: boolean) => !active);
    type.name(name);
    type.type(typeblock);
  };

  useEffect(() => {
    fetching();
  }, []);

  useEffect(() => {
    document.title = `${product?.brand.name} | ${product?.name}`;
  });

  return (
    <div className="oneproduct indent_last">
      <LoadingWin isLoading={isLoading}>
        <div className="container">
          <div className="indentstart"></div>
          <div className="oneproduct__wrap">
            <div className="oneproduct__row">
              <div className="oneproduct__pathblock">
                <div className="oneproduct__path">
                  <p
                    onClick={() => {
                      router(`/`);
                    }}
                    className="mini"
                  >
                    Home
                  </p>
                </div>
                <div className="oneproduct__path">
                  <p
                    onClick={() => {
                      router(`/shop`);
                    }}
                    className="mini"
                  >
                    Shop
                  </p>
                </div>
                <div translate="no" className="oneproduct__path">
                  <p
                    onClick={() => {
                      router(`/shop?type=${product?.typeId}`);
                    }}
                    translate="no"
                    className="mini"
                  >
                    {product?.type?.name}
                  </p>
                </div>
                <div translate="no" className="oneproduct__path">
                  <p
                    onClick={() => {
                      router(
                        `/shop?type=${product?.typeId}&brand${product?.brandId}=${product?.brandId}`
                      );
                    }}
                    translate="no"
                    className="mini"
                  >
                    {product?.brand?.name}
                  </p>
                </div>
                <div translate="no" className="oneproduct__path">
                  <p translate="no" className="mini">
                    {product?.name}
                  </p>
                </div>
              </div>

              <div className="oneproduct__item">
                <SliderApp product={product} />
                <div className="oneproduct__twoblock">
                  <div className="oneproduct__title">
                    <h3 translate="no">{product?.name}&nbsp;&nbsp;</h3>
                  </div>

                  <div className="oneproduct__brand">
                    <p className="mini">{product?.brand?.name}</p>
                  </div>
                  <div className="oneproduct__star">
                    <StarDevice
                      setElem={setElem}
                      text={'Be the first to review this product'}
                    />
                  </div>

                  <div
                    className={
                      product?.sale > 0
                        ? 'oneproduct__price add'
                        : 'oneproduct__price'
                    }
                  >
                    {!!product && <SaleProduct product={product} />}
                  </div>
                  <div className="oneproduct__add">
                    <div className="oneproduct__btn">
                      <Btn
                        product={product}
                        model={renderModal}
                        show={true}
                        item="oneitem"
                      />
                    </div>

                    {product && (
                      <HeartBtn
                        id={product.id}
                        model={renderModal}
                        one={true}
                      />
                    )}
                  </div>
                  <div className="oneproduct__about  about">
                    <ul className="about__text">
                      <li>
                        <p className="mini">Lorem ipsum dolor sit amet.</p>
                      </li>
                      <li>
                        <p className="mini">
                          Lorem ipsum dolor sit amet consectetur adipisicing.
                        </p>
                      </li>
                      <li>
                        <p className="mini">
                          Offers a snug, secure and supportive fit.
                        </p>
                      </li>
                    </ul>
                  </div>
                </div>
              </div>
            </div>
          </div>

          <Comments
            elem={elem}
            setElem={setElem}
            model={renderModal}
            product={product}
          />
        </div>
      </LoadingWin>
    </div>
  );
};

export { OneProduct };
