import { SingUpInputFiled } from '../../../../../../interface/ui.interface';
import FirstPageUp from './FirstPageUp';
import SecondPageUp from './SecondPageUp';
import './style/UpBody.scss';

const Body: React.FC<SingUpInputFiled> = ({
  setValue,
  register,
  errors,
  getValues,
  page,
  setPage,
  loading,
}) => {
  return (
    <div className="upbody">
      {page == 'first' ? (
        <FirstPageUp
          setValue={setValue}
          register={register}
          errors={errors}
          loading={loading}
        />
      ) : (
        <SecondPageUp
          setPage={setPage}
          setValue={setValue}
          register={register}
          errors={errors}
          getValues={getValues}
          loading={loading}
        />
      )}
    </div>
  );
};

export default Body;
